import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {NzMessageService, UploadFile} from 'ng-zorro-antd';
import {ResetHttpService} from '../../../share/util/reset-http.service';
import {ActivatedRoute} from '@angular/router';
import {UrlServeService} from '../../../share/util/url-serve.service';

@Component({
  selector: 'app-edit-experts',
  templateUrl: './edit-experts.component.html',
  styleUrls: ['./edit-experts.component.scss']
})
export class EditExpertsComponent implements OnInit {
  validateForm: FormGroup;
  patt = /\w{6,12}/;
  loading = false;
  sendData:any;
  avatarSmallUrl: string;
  avatarBiglUrl:any;
  imgUrl;
  smallImg;
  bigImg;

  constructor(private fb: FormBuilder,private http: ResetHttpService,private msg: NzMessageService,private route: ActivatedRoute,private url:UrlServeService) {
    this.validateForm = this.fb.group({
      sortNow: [ '', [ Validators.required ] ]
    });
    this.imgUrl = this.url.commentBaseUrl+this.url.imgUpLoad;
    this.route.queryParams.subscribe(params => {
      this.sendData = JSON.parse(params.data);
      this.avatarBiglUrl = this.sendData.big_pic;
      this.avatarSmallUrl = this.sendData.small_pic;
      this.validateForm.controls['sortNow'].setValue(this.sendData.sort);
    });
  }

  ngOnInit() {

  }
//提交
  submitForm = ($event, value) => {
    console.log(value,this.validateForm);
    $event.preventDefault();
    for (const key in this.validateForm.controls) {
      this.validateForm.controls[ key ].markAsDirty();
      this.validateForm.controls[ key ].updateValueAndValidity();
    }
    if(!this.validateForm.valid) this.validateForm.controls['sortNow'].setValue(this.sendData.sort);
    if(!this.smallImg || !this.bigImg) {
     this.smallImg = this.sendData.small_pic;
     this.bigImg = this.sendData.big_pic;
    }
    this.http.get(this.url.updateProfesser,{
      id:this.sendData.id,
      name:JSON.parse(sessionStorage.getItem('user')).name,
      sort:this.validateForm.controls[ 'sortNow' ],
      big_picture:this.smallImg,
      small_picture:this.bigImg,
      posiiton:'professor'
    },data=>{
      // console.log(data);
      if(data.code == '001000'){
        this.msg.success('添加成功！');
        history.back();
      }else{
        this.msg.error(data.message);
      }
    },err=>{
      this.msg.error(err);
    })

  };
  //取消
  cancleForm(e: MouseEvent): void {

  }
  //验证密码
  checkPhone = (control:FormControl): {[ s: string ]: boolean} =>{
    if(!control.value){
      return {required:true}
    }else if(!this.patt.test(control.value)){
      return { error: true };
    }
  };
  //上传图片
  beforeUploadSmall = (file: File) => {
    const isJPG = file.type === 'image/jpeg';
    const isPng = file.type === 'image/png';
    if(!(isPng || isJPG)){
      this.msg.error('只允许上传JPG 或者 PNG格式的图片');
    }
    const isLt2M = file.size / 1024 / 1024 < 1;
    if (!isLt2M) {
      this.msg.error('大小不能超过2MB!');
    }
    return (isJPG || isPng) && isLt2M;
  }
  beforeUploadBig(file:File){
    const isJPG = file.type === 'image/jpeg';
    const isPng = file.type === 'image/png';
    if(!(isPng || isJPG)){
      this.msg.error('只允许上传JPG 或者 PNG格式的图片');
    }
    const isLt2M = file.size / 1024 / 1024 < 1;
    if (!isLt2M) {
      this.msg.error('大小不能超过2MB!');
    }
    return (isJPG || isPng) && isLt2M;
  }

  private getBase64(img: File, callback: (img: {}) => void): void {
    const reader = new FileReader();
    reader.addEventListener('load', () => callback(reader.result));
    reader.readAsDataURL(img);
  }

  handleChangeSmall(info: { file: UploadFile }): void {
    if (info.file.status === 'uploading') {
      this.loading = true;
      return;
    }
    if (info.file.status === 'done') {
      if(info.file.response.code == '001000'){
        this.smallImg = info.file.response.response.filePath;
      }
      // Get this url from response in real world.
      this.getBase64(info.file.originFileObj, (img: string) => {
        console.log(img);
        this.loading = false;
        this.avatarSmallUrl = img;
      });
    }
  }
  handleChangeBig(info: { file: UploadFile }): void {
    if (info.file.status === 'uploading') {
      this.loading = true;
      return;
    }
    if (info.file.status === 'done') {
      if(info.file.response.code == '001000'){
        this.bigImg = info.file.response.response.filePath;
      }
      // Get this url from response in real world.
      this.getBase64(info.file.originFileObj, (img: string) => {
        console.log(img);
        this.loading = false;
        this.avatarBiglUrl = img;
      });
    }
  }
}
