import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {ExpertsMangerComponent} from './experts-manger/experts-manger.component';
import {AddExpertsComponent} from './add-experts/add-experts.component';
import {EditExpertsComponent} from './edit-experts/edit-experts.component';

const routes: Routes = [
  {path:'',component:ExpertsMangerComponent,data:{title: '专家管理'}},
  {path:'add-experts',component:AddExpertsComponent,data:{title: '添加专家'}},
  {path:'edit-experts',component:EditExpertsComponent,data:{title: '编辑专家'}}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ExpertsRoutingModule { }
