import {Component, OnInit, ViewChild} from '@angular/core';
import {Router} from '@angular/router';
import {ResetHttpService} from '../../../share/util/reset-http.service';
import {FormBuilder} from '@angular/forms';
import {NzMessageService, NzModalService} from 'ng-zorro-antd';
import {UtilServeService} from '../../../share/util/util-serve.service';
import {UrlServeService} from '../../../share/util/url-serve.service';

@Component({
  selector: 'app-experts-manger',
  templateUrl: './experts-manger.component.html',
  styleUrls: ['./experts-manger.component.scss']
})
export class ExpertsMangerComponent implements OnInit {
  dataSet:any = [];
  constructor(private msg:NzMessageService,private url :UrlServeService,private utilserve:UtilServeService,private fb:FormBuilder,private modalService:NzModalService,private router:Router,private http :ResetHttpService) { }

  ngOnInit() {
    console.log('加载');
    this.getData();
  }

//获取数据
  getData(pnum = '',rows = ''){
    this.http.get(this.url.professerList,{
      page:pnum,
      rows:rows
    },data=>{
      // console.log(data);
      if(data.code == '001000'){
        this.dataSet = data.response;
      }else{

      }
    },err=>{
      console.log(err);
    })
  }
  //停用
  isEnable(data){
    var isEnablemodal = this.modalService.create({
      nzTitle: '',
      nzContent: '关闭后App将不再显示！<br><br>确认关闭吗',
      nzClosable: false,
      nzOnOk: () => new Promise((resolve) => {
        this.http.get(this.url.updateProfesser,{
          id:data.id,
          status:'N'
        },data=>{
          if(data.code == '001000'){
            this.msg.success('操作成功！');
            this.getData();
            isEnablemodal.destroy();
          }else{
            this.msg.error(data.measure);
          }
        },err=>{
          console.log(err);
        });
      }),
      nzOnCancel:() => new Promise((resolve) =>{

        isEnablemodal.destroy();
      })
    });
  }
  //启用
  isable(data){
    this.http.get(this.url.updateProfesser,{
      id:data.id,
      status:'Y'
    },data=>{
      if(data.code == '001000'){
        this.msg.success('操作成功！');
        this.getData();
      }else{
        this.msg.error(data.measure);
      }
    },err=>{
      console.log(err);
    });
  }
  //跳转
  goPager(option){
    this.utilserve.goPage(option);
  }
  //删除
  deleteRow(i): void {
    this.http.get(this.url.deleProfesser,{
      id:i
    },data=>{
      // console.log(data);
      if(data.code == '001000'){
        this.msg.success('删除成功！');
        this.getData();
      }else{
        this.msg.error(data.message);
      }
    },err=>{
      console.log(err);
    })
  }
  //打开大图
  createModal(data): void {
    this.modalService.create({
      nzTitle: '',
      nzContent: `<img src="${data}" alt="" style="width: 100%;">`,
      nzClosable: false,
      nzFooter: null
    });
  }
}
